@mixin animate($duration:0.5s) {
	transition: all $duration ease-in-out;
}

@mixin set-background-image(){
	background: 
		linear-gradient(
			to right, 
			rgba($overlay-colour, 0.8),
			rgba($overlay-colour, 0.2)
		),
		url('../images/background.jpg') 10% 5px;
	background-size: cover;
	background-repeat: no-repeat;
}

@mixin btn {
	display: block;
	width: 9rem; 
	margin-top: 3rem;
	margin-left: auto;
	margin-right: auto;
	color: $white-colour;
	padding: 1rem;
	text-align: center;
	border-radius: $pill-corner;
	border: 1px solid $white-colour;


	&:hover, &:focus, &:active {
		background-color: $pink-colour;
		color: $white-colour;
		border-color: $pink-colour;
		transform: scale(1.2);
		@include animate(1s);
	}
}

@mixin contain {
	@include full-width;
	height: 100%;
}

@mixin full-width {
	width: 100%;
}

@mixin margin-center {
	margin-left: auto;
	margin-right: auto;
}

@mixin place-at-card-bottom ($includeTop:false) {
	position: absolute;
	bottom:0;
	right: 0;
	left: 0;

	@if $includeTop {
		top: 0;
	}
}

@mixin project-title {
	font-size: 1.5rem;
	font-weight: 700;
	color:$pink-colour;
	margin-bottom: 1rem;
}

@mixin section-background {
	background-color: rgba($black-colour, 0.75);
	padding: 3rem 1rem;
}
